const posts = (state = { posts: [], post: {} }, action) => {
  switch (action.type) {
    case "GET_POST_SUCCESS":
      return {
        ...state,
        post: action.post,
      };
    case "GET_POSTS_SUCCESS":
    case "DELETE_POSTS_SUCCESS":
      return {
        ...state,
        posts: action.posts,
        post: {},
      };
    case "ADD_POST_SUCCESS":
      return {
        ...state,
        posts: action.post.isApproved
          ? [...state.posts, action.post]
          : state.posts,
        post: {},
      };
    default: {
      return { ...state };
    }
  }
};

export default posts;
