import React, { memo, useState } from "react";
import { addPost } from "../store/actions";
import { useSelector, useDispatch, shallowEqual } from "react-redux";
import { useHistory } from "react-router-dom";
import Input from "./Input";
import Text from "./Text";

const AddPost = memo(() => {
  const [title, setTitle] = useState("");
  const [text, setText] = useState("");
  const { error } = useSelector((state) => state.errors, shallowEqual);
  const { user } = useSelector((state) => state.users, shallowEqual);
  const history = useHistory();
  const dispatch = useDispatch();

  const submitPostHandler = () => {
    if (title && text !== 0) {
      const post = {
        user: user.id,
        title,
        content: text,
        createdAt: new Date(),
      };
      dispatch(addPost(post, history));
    }
  };
  return (
    <div className="add-post">
      <h2>New Post</h2>
      <div className="add-post-form">
        <Input
          label="Title"
          onInput={(e) => setTitle(e.target.value)}
          value={title}
          hasError={error}
        />
        <Text
          label="Type in your thoughts"
          onUpdate={(txt) => setText(txt)}
          value={text}
          hasError={error}
        />

        <div className="char-count">{text.length} Characters</div>

        <button id="add-post-btn" onClick={submitPostHandler}>
          + Add
        </button>
      </div>
    </div>
  );
});

export default AddPost;
