import React, { memo } from "react";
import { Link } from "react-router-dom";
import PropTypes from "prop-types";
import moment from "moment";
import DeletePostButton from "./DeletePostButton";
import { useSelector, useDispatch, shallowEqual } from "react-redux";

import { deletePost } from "../store/actions";

const Post = memo(({ post }) => {
  const { _id, title, user, content, createdAt } = post;
  const { user: loggedInUser } = useSelector(
    (state) => state.users,
    shallowEqual
  );
  const dispatch = useDispatch();

  return (
    <div className="post">
      <Link to={`/post/${_id}`}>
        <h1>{title}</h1>
      </Link>
      <h3>
        by {user.name} | {moment(createdAt).format("ddd, MMM Do YY, h:mm a")}
      </h3>
      <p>{content.substring(0, 100)}...</p>
      {loggedInUser && user._id === loggedInUser.id ? (
        <DeletePostButton onConfirm={() => dispatch(deletePost(_id))} />
      ) : null}
    </div>
  );
});

Post.propTypes = {
  post: PropTypes.object.isRequired,
};

export default Post;
