import React, { useState } from "react";
import PropTypes from "prop-types";

const DeletePostButton = ({ onConfirm }) => {
  const [confirm, setConfirm] = useState(false);
  return (
    <div className="delete-post-btn">
      {!confirm ? (
        <button className="delete-btn" onClick={() => setConfirm(true)}>
          Delete?
        </button>
      ) : (
        <>
          <button className="delete-btn-yes" onClick={onConfirm}>
            Yes
          </button>
          <button className="delete-btn-no" onClick={() => setConfirm(false)}>
            No
          </button>
        </>
      )}
    </div>
  );
};

DeletePostButton.propTypes = {
  onConfirm: PropTypes.func
};

DeletePostButton.defaultProps = {
  onConfirm: () => {}
};

export default DeletePostButton;
